export const PlaySound = (src) => {
  if (canPlaySound) {
    var audio = new Audio(src);
    audio.play();
  }
}

export const scrollByPage = (page) => {
  let target = page * window.innerHeight;
  window.scrollTo({
    top: target,
    behavior: "smooth"
  })
}

class ChatBubble extends React.Component {

  constructor(props) {
    super(props)
    this.firstTime = true;
    this.filter = `hue-rotate(${360 * Math.random()}deg)`
    this.flex = `${Math.max(this.props.children.length, 6)} 1`
    this.left = `${this.props.left}em`
    this.bottom = `${this.props.bottom}em`
    this.openAnim = `chatBubbleEnter 0.5s cubic-bezier(0.26, 0.71, 0.21, 0.99) forwards`
    this.closeAnim = `chatBubbleOut 0.3s ease-out forwards`
    this.currentAnim = ''
  }

  componentDidMount() {
    this.firstTime = false;
    this.previous = this.props.animation;
  }

  componentDidUpdate() {
    this.previous = this.props.animation;
  }

  render() {

    let anim = '';
    if (this.firstTime) {
      anim = (this.props.animation) ? this.openAnim : this.currentAnim
    } else {
      anim = (this.props.animation) ? this.openAnim : (this.previous !== this.props.animation) ? this.closeAnim : this.currentAnim
    }
    this.currentAnim = anim;

    return (
      <div className='chatBubble' style={{
        animation: anim,
        marginLeft: this.left,
        marginBottom: this.bottom,
        filter: this.filter,
        flex: this.flex,
      }}>
        {this.props.children}
      </div>
    )
  }
}

class SigletoneExhibit extends React.Component {

  constructor(props) {
    super(props)
    this.TitleSpans = []

    let __state = {
      chatsState: []
    }

    if (this.props.title !== undefined) {
      for (let index = 0; index < this.props.title.length; index++) {
        const style = {
          display: "inline-block",
          transform: "scale(0, 0)",
          animation: `ScaleIn 0.5s cubic-bezier(0.04, 0.83, 0.35, 1.58) ${2 + (index * 0.1)}s forwards`,
        }
        const text = this.props.title.slice(index, index + 1);
        this.TitleSpans.push(<span style={style} key={"_title" + index + "_" + text}>{text}</span>)
      }
    }

    if (this.props.chats !== undefined) {
      const shuffle = (array) => {
        for (let i = array.length - 1; i > 0; i--) {
          let j = Math.floor(Math.random() * (i + 1));
          [array[i], array[j]] = [array[j], array[i]];
        }
        return array.slice()
      }
      const createRandomArray = (a, b, step = 1) => {
        const min = (a >= b) ? b : a;
        const max = (min === b) ? a : b;
        let _return = []
        for (let i = min; i < max; i += step) {
          _return.push(i)
        }
        return shuffle(_return);
      }
      let left = createRandomArray(-5, 0)
      let bottom = createRandomArray(10, 20, 2)
      const getValue = (of = [], index) => {
        const i = index % of.length; //循環取得
        const val = of[i];
        return val + ((Math.random() * 0.5) * ((Math.random > 0.5) ? -1 : 1)); //取得±0.5
      }
      this.left = []
      this.bottom = []
      this.str = []

      for (let index = 0; index < this.props.chats.length; index++) {
        const str = this.props.chats[index];
        __state.chatsState.push(false);
        this.left.push(getValue(left, index))
        this.bottom.push(getValue(bottom, index))
        this.str.push(str)
      }
    }

    if (this.props.picOffset !== undefined) {
      this.picOffset = this.props.picOffset;
    } else {
      this.picOffset = ["100%", "100%"];
    }
    if (this.props.picUrl !== undefined) {
      this.picUrl = this.props.picUrl;
    }

    this.state = __state;
  }

  componentDidMount() {

    ///此算式為每個框框的顯示時間，數值越大，代表畫面上有框框的數量會越多
    const DurationMin = 3000; //最少顯示3秒
    const DurationMax = 5000; //最多顯示8秒
    const DisplayDuration = (index) => { return DurationMin + DurationMax * Math.random() }

    ///此算式為框框從關閉到展開需要等待的時間，數值越大，代表框框的空窗期會越長
    const SleepDurationMin = 2000; //最少休息2秒
    const SleepDurationMax = 3000; //最多休息4秒
    const VariationInterval = (index) => { return SleepDurationMin + SleepDurationMax * Math.random() + (1000 * index) }

    const SetOpenEvent = (index) => {
      setTimeout(() => {
        let state = this.state;
        state.chatsState[index] = true;
        this.setState(state);
        SetCloseEvent(index)
      }, VariationInterval(index));
    }

    const SetCloseEvent = (index) => {
      setTimeout(() => {
        let state = this.state;
        state.chatsState[index] = false;
        this.setState(state);
        SetOpenEvent(index)
      }, DisplayDuration(index));
    }

    this.state.chatsState.forEach((e, i) => {
      SetOpenEvent(i)
    })
  }

  render() {

    let chats = []
    if (this.props.chats !== undefined) {
      for (let index = 0; index < this.props.chats.length; index++) {
        const str = this.props.chats[index];
        chats.push(
          <ChatBubble
            animation={this.state.chatsState[index]}
            left={this.left[index]}
            bottom={this.bottom[index]}
            index={index}
            key={index + str}>
            {str}
          </ChatBubble>
        )
      }
    }

    const page = {
      flex: 1,
      display: "flex",
      width: "100%",
      height: "100%",
    };

    const lcolumn = {
      flex: '1 1 0%',
      display: 'flex',
      placeContent: 'center',
      alignItems: 'flex-end',
      flexDirection: 'column',
      alignContent: 'flex-end',
      flexWrap: 'wrap',
      justifyContent: 'space-evenly'
    }
    const lcolumn_mirror = {
      flex: '1 1 0%',
      display: 'flex',
      placeContent: 'center',
      alignItems: 'flex-end',
      flexDirection: 'column',
      alignContent: 'flex-start',
      flexWrap: 'wrap',
      justifyContent: 'space-evenly'
    }
    const rColumn = {
      flex: '0.9 1 0%',
      display: 'flex',
      placeContent: 'flex-end center',
      flexDirection: 'column',
      alignItems: 'flex-end',
      justifyContent: 'flex-end',
      alignContent: 'flex-end'
    }

    const pic = {
      backgroundImage: `url(${this.picUrl})`,
      backgroundRepeat: "no-repeat",
      backgroundPosition: `${this.picOffset[0]} ${this.picOffset[1]}`,
      backgroundColor: `transparent`
    }
    if (this.props.mirror) {
      return (
        <div style={page}>
          <div className='infoPictureArea' style={pic}></div>
          <div style={rColumn}>
            <div className='chats'>{chats}</div>
          </div>
          <div style={lcolumn_mirror}>
            <div className='infoTitle'>
              {this.TitleSpans}
            </div>
            <div className='infocontent'>
              {this.props.children}
              <div className='FlexHoverToScale'>
                <div className='FlexNextButton' onClick={() => { scrollByPage(this.props.page + 1); }}></div>
              </div>
            </div>
          </div>
        </div>
      );
    }
    else {
      return (
        <div style={page}>
          <div className='infoPictureArea' style={pic}></div>
          <div style={lcolumn}>
            <div className='infoTitle'>
              {this.TitleSpans}
            </div>
            <div className='infocontent'>
              {this.props.children}
              <div className='FlexHoverToScale'>
                <div className='FlexNextButton' onClick={() => { scrollByPage(this.props.page + 1); }}></div>
              </div>
            </div>
          </div>
          <div style={rColumn}>
            <div className='chats'>{chats}</div>
          </div>
        </div>
      );
    }
  }
}

