import React from 'react';
import { getFrom } from '../mabinogi3th_characters';
import { ExhibitCharacter } from '../components/ExhibitCharacter';
import logoType from '../img/logotype.png';
import { keyframes } from 'glamor';
import { scrollByPage } from '../index';

export class Cover extends React.Component {
  render() {
    let chs = [];
    const NameList = {
      rice: "小色米",
      warm: "一世溫言",
      train: "苗兒",
      post: "信箱",
      banana: "牧小蕉",
      blue: "沁羽藍",
      seven: "琪稀",
      deer: "蘑菇鹿",
      eight: "夜葬八荒"
    };
    let translate = 100;
    let delay = 0;
    for (const [key, value] of Object.entries(NameList)) {
      const data = getFrom(key, 0, "homepageLayout");
      data.offset.y -= 5;
      chs.push(<ExhibitCharacter img={data} key={key} translate={translate} delay={delay}></ExhibitCharacter>);
      translate = (translate === 100) ? -100 : 100;
      delay += 0.1;
    }

    const LogoKeyFrame = keyframes({
      '0%': { transform: `translate(0px,5em)`, opacity: 0 },
      '100%': { transform: "translate(0px,0px)", opacity: 1 }
    });
    const LogoIdle = keyframes({
      '0%': { filter: "brightness(1) drop-shadow(0 0 2rem white)" },
      '100%': { filter: "brightness(1.5) drop-shadow(0 0 2rem white)" }
    });
    const Fadeout = keyframes({
      '0%': { opacity: 1 },
      '100%': { opacity: 0 }
    });
    const Fadein = keyframes({
      '0%': { opacity: 0 },
      '100%': { opacity: 1 }
    });
    const ScaleIn = keyframes({
      '0%': { transform: "rotate(138deg) scale(1,0)", filter: "blur(10px)" },
      '100%': { transform: "rotate(138deg) scale(1,1)", filter: "blur(0px)" }
    });
    const decoration_back = {
      position: "relative",
      width: "300%",
      height: "20%",
      background: "linear-gradient(to right, rgb(255 255 255 / 100%), rgb(255 255 255 / 0%))",
      right: "72%",
      top: "-95%",
      transform: "rotate(138deg) scale(0,0)",
      animation: `${ScaleIn} 1s 3.2s`,
      animationFillMode: "forwards"
    };
    const decoration_front = {
      width: "300%",
      height: "20%",
      background: "linear-gradient(to right, rgb(255 255 255 / 100%), rgb(255 255 255 / 0%))",
      position: "relative",
      right: "9%",
      top: "-80%",
      transform: "rotate(138deg) scale(0,0)",
      animation: `${ScaleIn} 0.5s 3s`,
      animationFillMode: "forwards"
    };
    const decoration_front_slim = {
      width: "300%",
      height: "5%",
      background: "linear-gradient(to right, rgb(255 255 255 / 100%), rgb(255 255 255 / 0%))",
      position: "relative",
      right: "4%",
      top: "-80%",
      transform: "rotate(138deg) scale(0,0)",
      animation: `${ScaleIn} 0.5s 3.2s`,
      animationFillMode: "forwards"
    };
    const Logotype = {
      backgroundImage: `url(${logoType})`,
      width: "40em",
      height: "20em",
      backgroundSize: "contain",
      position: "absolute",
      left: "calc( 100% - 40em)",
      top: "calc( 100% - 30em )",
      opacity: 0,
      animation: `${LogoKeyFrame} 1s ease 3.5s, ${LogoIdle} 2s ease-in-out 1s infinite alternate`,
      animationFillMode: "forwards",
    };
    const copyRight = {
      width: "52em",
      height: "10em",
      color: "#FFFFFF",
      position: "absolute",
      letterSpacing: "0.1em",
      fontSize: "0.5em",
      lineHeight: "3em",
      right: 0,
      bottom: 0,
      opacity: 0,
      animation: `${Fadein} 1s 5s`,
      animationFillMode: "forwards"
    };
    const preloadMask = {
      position: "absolute",
      top: "0",
      width: "100%",
      height: "100%",
      backgroundColor: "#FFFFFF",
      backgroundBlendMode: "lighten",
      animation: `${Fadeout} 3s ease 1s`,
      animationFillMode: "forwards",
      pointerEvents: "none"
    };
    const page = {
      width: "100%",
      height: "100%",
    };
    const pageMask = {
      position: "absolute",
      width: "100%",
      height: "100%",
      overflow: "hidden"
    };
    const createParticle = (scale) => {
      const _45 = `${45 * scale}`;
      const _15 = `${15 * scale}`;
      const _30 = `${30 * scale}`;
      const _60 = `${60 * scale}`;
      const _105 = `${105 * scale}`;
      const _90 = `${90 * scale}`;
      const _particleAnim = keyframes({
        '0%': { transform: "scale(0,0)", filter: "brightness(1)" },
        '100%': { transform: "scale(1,1)", filter: "brightness(1.2)" }
      });
      return {
        position: "absolute",
        left: `${Math.random() * 80}%`,
        top: `${Math.random() * 80}%`,
        width: `${58 * scale}px`,
        height: `${90 * scale}px`,
        backgroundColor: "rgb(255, 255, 255)",
        transform: "scale(0,0)",
        clipPath: `path('M 0 ${_45} C ${_15} ${_45} ${_30} ${_30} ${_30} 0 C ${_30} ${_30} ${_45} ${_45} ${_60} ${_45} C ${_30} ${_45} ${_30} ${_90} ${_30} ${_105} C ${_30} ${_90} ${_30} ${_45} 0 ${_45} Z')`,
        animation: `${_particleAnim} ${0.4}s ease-in-out ${Math.random() * 5}s infinite alternate`
      };
    };
    let particles = [];
    let particles_amount = 15 + (Math.random() * 15);
    for (let index = 0; index < particles_amount; index++) {
      particles.push(<div style={createParticle(Math.random() * 0.5 + 0.1)} key={`particles${index}`}></div>);
    }
    const outerGlow = {
      filter: "drop-shadow(0 0 0.75rem yellow)",
      width: "100%",
      height: "100%",
      overflow: "hidden",
      position: "absolute",
      top: 0,
      pointerEvents: "none"
    };
    return (
      <div style={page}>
        <div style={pageMask}>
          <div style={decoration_back}></div>
        </div>
        {chs}
        <div style={decoration_front}></div>
        <div style={decoration_front_slim}></div>
        <div style={Logotype}></div>
        <div className='HoverToScale'>
          <div className='NextButton' onClick={() => { console.log("!"); scrollByPage(1); }}></div>
        </div>
        <div style={copyRight}>
          本企劃有部分素材源自由韓國devCat所開發的遊戲新瑪奇Mabinogi。<br></br>
          除外則為Mabinogi之中玩家角色的二次創作。<br></br>
          本企劃為貝婷的疊字友友們共同製作。
        </div>
        <div style={outerGlow}>
          {particles}
        </div>
        <div style={preloadMask}></div>
      </div>
    );
  }
}
